@charset "UTF-8";
@import "../libs/Just-validate-master/dist/css/justValidateTooltip.min.css";
@font-face {
  font-family: "roboto-bold";
  src: url("../fonts/roboto-bold-webfont.woff2") format("woff2"), url("../fonts/roboto-bold-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "roboto-regular";
  src: url("../fonts/roboto-regular-webfont.woff2") format("woff2"), url("../fonts/roboto-regular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "fn-bold";
  src: url("../fonts/ttfirsneuebold-webfont.woff2") format("woff2"), url("../fonts/roboto-regular-webfont.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "fn-regular";
  src: url("../fonts/ttfirsneueregular-webfont.woff2") format("woff2"), url("../fonts/ttfirsneueregular-webfont.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
body {
  font-family: "roboto-regular", sans-serif;
}

body, h1, h2, h3 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

* {
  box-sizing: border-box;
  background-repeat: no-repeat;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

input {
  border: transparent;
  background-color: #4A4A4A;
  transition-property: border-color;
  transition-duration: 0.4s;
  transition-timing-function: linear;
}

.input-field::placeholder {
  transition-property: color;
  transition-duration: 0.4s;
  transition-timing-function: linear;
}

input::placeholder {
  font-family: "roboto-regular", sans-serif;
}

a {
  transition-property: color;
  transition-duration: 0.4s;
  transition-timing-function: linear;
}

svg {
  transition-property: fill;
  transition-duration: 0.4s;
  transition-timing-function: linear;
}

a:link {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

.img-label {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 6px;
  right: 8px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 14px;
  padding-right: 14px;
  height: auto;
  font-size: 14px;
  background-color: #333333;
}
.img-label a {
  color: #FFFFFF;
}
.img-label a:hover {
  color: #FF6E30;
}

.calendar-icon {
  background-image: url("../img/calendar-icon.png");
  background-position: left;
  font-family: "roboto-regular", sans-serif;
  color: #999999;
}

.calendar-icon-img {
  padding-right: 15px;
  background-image: url("../img/calendar-icon.png");
  color: #ffffff;
}

.calendar-icon, .calendar-icon-img {
  padding-left: 20px;
  font-size: 12px;
  transition-property: color;
  transition-duration: 0.4s;
  transition-timing-function: linear;
}
.calendar-icon:hover, .calendar-icon-img:hover {
  color: #FF6E30;
}

.time-icon {
  padding-left: 18px;
  background-image: url("../img/time.png");
  background-position: left;
  font-size: 12px;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;
}

.btn {
  font-family: "roboto-bold", sans-serif;
  font-size: 16px;
  cursor: pointer;
  transition-property: background-color;
  transition-duration: 0.4s;
  transition-timing-function: linear;
}

.know-how:hover {
  background-color: #BC572B;
}

.know-how:active {
  background-color: #FF6E30;
  border: 3px solid #CE4307;
}

.input-field {
  padding-left: 23px;
  font-size: 16px;
  color: #202020;
  outline: none;
}
.input-field:hover::placeholder {
  color: #FF6E30;
}
.input-field:hover:focus {
  border: none;
  outline: 1px solid #FF6E30;
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  outline: 2px solid red;
  width: 1px;
  height: 1px;
  margin: -1px;
}

.box-close-btn {
  position: absolute;
  border: none;
  width: 20px;
  height: 20px;
  background-color: transparent;
  background-image: url("../img/close_btn.png");
  z-index: 20;
}

@media (max-width: 320px) {
  .img-label {
    padding-top: 3px;
    padding-bottom: 3px;
    font-size: 12px;
  }
}
.master-class {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}

.master-class__right-content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 23px 25px;
}

.master-class__img:nth-child(3n+1) {
  grid-column: span 2;
  grid-auto-rows: minmax(200px, 300px);
}

.about-studio__content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 29px;
}

.our-projects__part {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-auto-rows: minmax(300px, auto);
  gap: 30px 26px;
}

.our-projects__small-img {
  grid-column: span 2;
}

.our-projects__big {
  grid-column: span 6;
}

.our-projects__medium-img {
  grid-column: span 3;
}

.services__section-tab1 {
  display: grid;
  grid-template-columns: 1fr 410px;
  gap: 20px 26px;
}

.services__section-tab1-left {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: min-content;
  gap: 20px;
}

.services__section-tab2 {
  display: grid;
  grid-template-columns: 1fr 410px;
  gap: 28px 26px;
}

.services__section-tab2-left {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: min-content;
  gap: 24px 25px;
}

.services__right-list-imgs {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 25px 30px;
}

.master-classes__img {
  grid-area: master-img;
}

.master-classes__text {
  grid-area: master-text;
}

.master-classes__imgs-list {
  grid-area: master-imgs;
}

.master-classes__section {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: minmax(330px, auto);
  gap: 30px;
  grid-template-areas: "master-img  master-text  master-imgs";
}

.master-classes__imgs-list {
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: min-content;
  grid-row-gap: 28px;
}

.contacts {
  display: grid;
  grid-template-columns: 66% 1fr;
  gap: 30px;
}

.footer__logo {
  grid-area: logo;
}

.footer__text {
  grid-area: _text;
}

.footer__social {
  grid-area: social;
}

.footer__top {
  display: grid;
  grid-template-columns: 263px 402px 164px;
  grid-template-rows: minmax(66px, auto);
  gap: 231px;
  grid-template-areas: "logo _text social";
}

.container {
  max-width: 1920px;
  min-width: 320px;
}

.content {
  max-width: 1290px;
  margin: 0 auto;
}

.just-validate-tooltip-container label {
  position: relative;
}
.just-validate-tooltip-container .js-validate-error-label {
  position: absolute;
  top: 0;
  left: 24px;
  font-size: 10px;
}

/* -------header------------------- */
.header {
  width: 100%;
}

.header__top {
  min-height: 80px;
  padding: 21px 0;
  background-color: #202020;
}

.header__top-content {
