}

.header-phone:hover {
  color: #FF6E30;
}

.header-phone:active {
  color: #FFFFFF;
  background-color: #bc572b;
}

.header-phone:visited {
  color: #CE4307;
  background-image: url("../img/phone_visited.svg");
}

.header__finder {
  position: relative;
  transform: translateY(-3px);
}

.header__search-btn {
  position: absolute;
  border: none;
  width: 25px;
  height: 25px;
  top: 2px;
  left: 10px;
  fill: #CACACA;
}

.header__search-btn-close {
  display: none;
}

.header__burger-btn-close {
  display: none;
}

.header__finder-input768 {
  display: none;
}

.header__search-btn768 {
  display: none;
}

.header__search-btn:hover {
  fill: #FF6E30;
}

.header__text-find {
  margin-left: 35px;
  width: 171px;
  border-bottom: 1px solid #e9e9e9;
  color: #e9e9e9;
}

.header__text-find:hover {
  border-bottom: 1px solid #FF6E30;
}

.header__text-find:focus {
  outline: none;
