@import "variables.scss";
@import "common.scss";
@import "grids.scss";
@import "../libs/Just-validate-master/dist/css/justValidateTooltip.min.css";

.container {
  max-width: 1920px;
  min-width: 320px;
}

.content {
  max-width: 1290px;
  margin: 0 auto;
}

.just-validate-tooltip-container {
  label {
    position: relative;
  }
  .js-validate-error-label {
    position: absolute;
    top:0;
    left: 24px;
    font-size: 10px;
  }
}

/* -------header------------------- */

.header {
  width: 100%;
}

.header__top {
  min-height: 80px;
  padding: 21px 0;
  background-color: $section_color;
}

.header__top-content {
  display: flex;
  height: 100%;
  align-items: center;
}

.header__logo {
  margin-right: 98px;
  width: 191px;
}

.header__bottom {
  min-height: 42px;
  padding: 10px 0 9px;
  background-color: #4a4a4a;
}

.header__bottom-content {
  display: flex;
  height: 100%;
  justify-content: space-between;
  align-items: center;
}

.header__bottom-phone-box {
  width: 50%;
}

.header__bottom-phone {
  display: inline-block;
  transition-property: color;
  transition-duration: 0.4s;
  color: $content_text_color;
  min-width: 200px;
  max-width: 670px;
  &:hover {
    color: $btn_color;
  }
}

.header__bottom-phone768 {
  display: none;
}

.header__text-find::placeholder {
  font-family: "fn-regular", sans-serif;
  font-size: 14px;
  color: #e9e9e9;
}

.header__menu {
  width: 69%;
  font-family: "fn-regular", sans-serif;
}

.header__menu768 {
  display: none;
}

.header__menu-items {
  display: flex;
  flex-direction: row;
  justify-content: left;
  flex-wrap: wrap;

  a {
    font-size: 16px;
    color: $label_text_color;
  }
}

.header__menu-item {
  display: flex;
  justify-content: center;
  margin-right: 21px;
  width: 146px;
}

.header__menu {
  a:hover {
    color: $btn_color;
  }
}

.header__menu {
  a:active {
    color: $label_text_color;
    background-color: #bc572b;
  }
}

.header__menu {
  a:visited {
    color: $visited_link;
  }
}

.header__menu-hidden {
  display: none;
}

.logo svg:first-child {
  margin-right: 10px;
}

.sociallink {
  display: flex;
  padding-top: 4px;
  width: 135px;
  justify-content: left;
  flex-wrap: wrap;
}

.sociallink__item-link {
  color: $content_text_color;
}

.sociallink__item-link:hover {
  color: $btn_color;
}

.sociallink__item-link:active {
  color: $visited_link;
}

.sociallink__item:not(:nth-child(3n)) {
  margin-right: 20px;
}

