.header-phone:link {
  margin-right: 20px;
  font-family: "fn-bold", sans-serif;
  color: #e9e9e9;
  background-position: left;
  background-size: 70%;
}

.header-phone:hover {
  color: $btn_color;
}

.header-phone:active {
  color: $label_text_color;
  background-color: #bc572b;
}

.header-phone:visited {
  color: $visited_link;
  background-image: url("../img/phone_visited.svg");
}

.header__finder {
  position: relative;
  transform: translateY(-3px);
}

.header__search-btn {
  position: absolute;
  border: none;
  width: 25px;
  height: 25px;
  top: 2px;
  left: 10px;
  fill: $content_text_color;
}

.header__search-btn-close {
  display: none;
}

.header__burger-btn-close {
  display: none;
}
.header__finder-input768 {
  display: none;
}

.header__search-btn768 {
  display: none;
}

.header__search-btn:hover {
  fill: $btn_color;
}

.header__text-find {
  margin-left: 35px;
  width: 171px;
  border-bottom: 1px solid #e9e9e9;
  color: #e9e9e9;
}

.header__text-find:hover {
  border-bottom: 1px solid $btn_color;
}

.header__text-find:focus {
  outline: none;
  border: none;
  border-bottom: 1px solid $visited_link;
}

