// -----------Сервисы---------------------
.services {
  padding-bottom: 34px;
}

.services__top {
  margin-bottom: 34px;
}

.services__top-title {
  margin-bottom: 9px;
  max-width: 56%;
  font-family: "fn-bold", sans-serif;
  font-size: 24px;
  color: $section_color;
}

.services__title {
  margin-bottom: 11px;
  font-family: "fn-bold", sans-serif;
  font-size: 24px;
  color: $section_color;
}

.services__tab-box {
  width: 32%;
}
.services__tab-left,
.services__tab-right {
  padding: 0;
}

.services__tabs {
  display: flex;
  margin-bottom: 5px;
  width: 100%;
  justify-content: space-between;
}

.services__tabs-line {
  position: relative;
  width: 100%;
  height: 4px;
  background-color: #c4c4c4;
}

.services-section {
  display: none;
  transition-property: opacity;
  transition-duration: 1s;
  transition-timing-function: linear;
  opacity: 0;
}

.services__tabs-move {
  position: absolute;
  transition-property: left;
  transition-duration: 0.7s;
  transition-timing-function: linear;
  left: 0;
  width: 145px;
  height: 100%;
  background-color: $btn_color;
}

.services__tab {
  border: none;
  color: #666666;
  background-color: transparent;
  transition-property: color;
  transition-duration: 0.4s;
  transition-timing-function: linear;
  &:hover {
    color: $btn_color;
  }
  &:active {
    background-color: $active_bgcolor;
  }
}

.tab-active {
  color: $btn_color;
}

.services__discript-link {
  transition-property: width;
  transition-duration: 0.5s;
  transition-timing-function: linear;
  display: inline-block;
  background-position: right;
  width: 105px;
  font-family: "roboto-bold", sans-serif;
  &:link {
    color: $btn_color;
    background-image: url("../img/arrow_1.png");
  }
  &:hover {
    width: 115px;
    background-image: url("../img/arrow1_hover.png");
  }
  &:active {
    background-color: $active_bgcolor;
  }
  &:visited {
    color: $visited_link;
    background-image: url("../img/arrow_1_1.png");
  }
}

.services__discript-title {
  margin-bottom: 5px;
  font-family: "fn-regular", sans-serif;
  font-size: 22px;
  color: $section_color;
  &:hover {
    color: $btn_color;
  }
  &:active {
    color: $section_color;
    background-color: $active_bgcolor;
  }
  &:visited {
    color: $visited_link;
  }
}
.services__discript-title-tab2 {
  font-size: 24px;
  line-height: 24px;
}

.services__right-img {
  margin-top: -105px;
}

.services__discription {
  margin-bottom: 15px;
  font-size: 16px;
  color: #999999;
}

.services__section-tab1-left {
  .services__discript-tab1-item {
    display: flex;
  }
  .services__discript-img-tab1 {
    width: 55%;
  }
  .discript-text {
    padding-left: 31px;
    width: 100%;
  }
}

.services__section-tab2-item {
  display: flex;
  justify-content: space-between;
  .services__discript-img {
